import React, { Fragment, Component } from 'react';
import style from "../../../components/SuccessPage/success.module.css";

import CircleBtn from "../../../components/CircleBtn";


import api from "../../../util/api";
import faceLike from "../../../assets/img/recommendation/face-like.png";
import faceDislike from "../../../assets/img/recommendation/face-dislike.png";
import happyFace from "../../../assets/img/recommendation/happy-fave.svg";
import sadFace from "../../../assets/img/recommendation/sad-face.svg";



class Recommendation extends Component {

    constructor(props) {
        super(props);

        this.state = {
            recommendation: null,
            pending: true,
            rate: false,
            detailedView: false,

        }
    }

    componentDidMount() {
        this.getRecommendation()
    }


    getRecommendation = () => {

        this.setState({
            pending: true
        })

        api.get(`recommendation`).then(({data}) => {
            this.setState({
                recommendation: data.data,
                pending: false
            })
        }).catch(() => {
            this.setState({
                pending: false
            })
        })
    }

    setRate = (rate = true) => {
        this.setState({
            rate
        })
    }

    render() {

        const { categories, rate, detailedView } = this.state


        if (this.props.subscribeSlide) {

            return  (
                <div className={style['recommendation-over']}>
                    <h2 style={{
                        fontSize: '1.85rem',
                        marginBottom: '20px',
                        fontWeight: 300
                    }}>
                        Free recommendations
                        is over.
                    </h2>
                    <p  style={{
                        fontSize: '1.12rem',
                        marginBottom: '30px'
                    }}>
                        If you want more recommendations please subscribe
                    </p>
                    <p style={{
                        fontSize: '10rem',
                        marginBottom: 0,
                        marginTop: 0,
                        lineHeight: 1
                    }}>
                        5$
                    </p>
                    <p style={{
                        fontSize: '1rem',
                        marginBottom: '30px',
                    }}>
                        per month
                    </p>
                    <button className={style.subsribeBtn}>
                        subsribe
                    </button>
                </div>
            )
        }



        return (
            <div className={style.wrap} style={{
                backgroundImage: `url(${this.props.photos[0]})`
            }}>
                {rate &&
                    <div className={style.rate}>
                        <div className={style['rate-title']}>
                            rate this point
                        </div>
                        <div>
                            <a href="#" className={style['link-white']} onClick={(e) => {
                                e.preventDefault()
                                this.setRate(false)
                            }}>
                                rate later
                            </a>
                        </div>
                        <div className={style['rate-faces']}>
                            <button className={style['rate-face-btn']} onClick={() => {
                                this.setRate(false)
                            }}>
                                <img src={happyFace} alt=""/>
                            </button>
                            <div className={style['rate-faces-sep']}>
                                or
                            </div>
                            <button className={style['rate-face-btn']} onClick={() => {
                                this.setRate(false)
                            }}>
                                <img src={sadFace} alt=""/>
                            </button>
                        </div>

                        <div style={{
                            marginTop: 'auto',
                            marginBottom: '20px',
                        }}>
                            <a href="#" className={style['link-pink']} onClick={(e) => {
                                e.preventDefault()

                                api.post(`recommendation/skip`, {
                                    recommendationId: this.props.id
                                }).then(({data}) => {
                                    this.setState({
                                        recommendation: data.data,
                                        pending: false
                                    })
                                })


                                this.setRate(false)
                            }}>
                                skip
                            </a>
                        </div>
                    </div>
                }
                <div className={style.content}>
                    <div className={style['content-inner']}>

                        {detailedView ?

                            <Fragment>
                                <div />
                                <div className={style['detailed-list']}>
                                    <div className={style['detailed-block']}>
                                        <div style={{
                                            fontSize: '1.25rem'
                                        }}>
                                            {this.props.title}
                                        </div>
                                        <div style={{
                                            color: '#C432AC'
                                        }}>
                                            {this.props.address}
                                        </div>
                                        <div style={{
                                            color: '#7935CB',
                                            fontWeight: 300
                                        }}>
                                            {this.props.latitude}, {this.props.latitude}
                                        </div>
                                    </div>
                                    <div className={style['detailed-block']}>
                                        <div style={{
                                            fontSize: '1.25rem'
                                        }}>
                                            {this.props.workHours.from}-{this.props.workHours.until}
                                        </div>
                                        <div style={{
                                            color: '#FEBD38',
                                        }}>
                                            average check: {this.props.averageBillUSD}$
                                        </div>
                                    </div>
                                    <div className={style['detailed-block']}>
                                        <div style={{
                                            marginBottom: '4px'
                                        }}>
                                            <a href= {this.props.website} target="_blank"
                                               style={{
                                                   color: '#7935CB',
                                               }}
                                               rel="noopener nofollow">
                                                {this.props.website}
                                            </a>
                                        </div>
                                        <div style={{
                                            marginBottom: '4px'
                                        }}>
                                            {this.props.phoneNumber}
                                        </div>
                                        <div>
                                            <a href= {this.props.googleLink} target="_blank"
                                               style={{
                                                   color: '#7935CB',
                                               }}
                                               rel="noopener nofollow">
                                                see on map
                                            </a>
                                        </div>


                                    </div>
                                    <div className={style['detailed-block']}>
                                        {this.props.description}
                                    </div>
                                </div>
                            </Fragment> :
                            <Fragment>
                                <div className={style['content-top']}>
                                    <div className={style.col}>
                                        <div className={style.title}>
                                            {this.props.title}
                                        </div>
                                        <div className={style.workHours}>
                                            {this.props.workHours.from}-{this.props.workHours.until}
                                        </div>
                                    </div>
                                    <div className={style['right-col']}>
                                        <div className={style.rating}>
                                            <div>
                                                 user rating
                                            </div>
                                            <div>
                                                <button className='button-clear-defaults'
                                                onClick={this.setRate}><img src={faceLike} alt=""/></button> +  {this.props.positiveReviews || 'N/A'}
                                            </div>
                                            <div>
                                                <button className='button-clear-defaults'
                                                        onClick={this.setRate}><img src={faceDislike} alt=""/></button> - {this.props.negativeReviews || 'N/A'}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className={style['content-bottom']}>
                                    <div className={`${style['content-bottom-item']} ${style.cost}`}>
                                        average check: {this.props.averageBillUSD}$
                                        <div className={style.visited}>
                                            <div className={style['compare-card-photo--inner']}>
                                                {this.props.lastFiveUsersSelectedThisLocation.map((account, i) => {

                                                    return (
                                                        <div className={style['compare-card-photo--image']} key={i}>
                                                            <div className={style['avatar-img-wrap']} >
                                                                <img src={account} className={`${style['avatar-img']}`} />
                                                            </div>
                                                        </div>

                                                    )
                                                })}
                                            </div> <div style={{
                                                marginLeft: '15px'
                                        }}>
                                            +{this.props.numberOfUsersSelectedThisLocation}
                                        </div>
                                        </div>
                                    </div>
                                    <div className={style['content-bottom-item']}>
                                        <CircleBtn size={60} fontSize={'0.67rem'}
                                                   onClick={() => {
                                                       this.setState({
                                                           detailedView: true
                                                       })
                                                   }}
                                                   style={{
                                                       fontWeight: 300
                                                   }}>
                                            Go <br/>
                                            here
                                        </CircleBtn>
                                    </div>
                                    <div className={style['content-bottom-item']} >

                                    </div>
                                </div>
                            </Fragment>
                        }
                    </div>
                </div>
            </div>
        )
    }

}

export default Recommendation