import React, { Component } from 'react';

import CircleBtn from "../../components/CircleBtn";

import api from '../../util/api'

import style from './auth.module.css'

import logo from '../../assets/img/auth/auth_logo.png'


class Auth extends Component{

    constructor(props) {
        super(props);

        this.state = {
            pending: false
        }

    }

    openLoginWindow = () => {
        const w = 500
        const h = 500

        const left = (window.width/2)-(w/2);
        const top = (window.height/2)-(h/2);

        const newWin = window.open(`about:blank`, "auth", `toolbar=no, location=no, directories=no, status=no, menubar=no, scrollbars=no, resizable=no, copyhistory=no, width=${w}, height=${h}, top=${top}, left=${left}`);

        this.onLogin()

        newWin.location.href = 'https://www.instagram.com/oauth/authorize?client_id=476859443194257&redirect_uri=https://freshplan.ru/callback&scope=user_profile&response_type=code'

       const interval = setInterval(() => {
           const location = new URL(window.location)

           try {
               if (location.searchParams.get('code')) {
                   clearInterval(interval)
                   newWin.close()
                   this.onLogin()
               }
           } catch (e) {
               console.log(e)
           }

       }, 1000)

        newWin.onbeforeunload = function () {
            clearInterval(interval)
            newWin.close()
        };

    }

    onLogin = () => {


        // const code = 'AQCES55m47KC1y9JKn1T7_GKWnOt9Tg-tj5Mj5ZxB0E4NjctXrIgItKd4cfyGoZZ8HZKO7C7QOIXgcUSxD6R-r0_qF3pz7biIzOyTb68ZgMcEeI1kv978nRdIN_Ti5rhMjawi_rsdKmN4EUlKZ3tR5lsn5-oGLOmJ937Ushy_YzVApYLTvtsze3Y_Sj8Y2t3TmhOdyHp0WyTPmf40KrjFP4WsKyOOVdS54gTQUd6aE3MQg#_'
        const code = 'AQBx5zWT5KlGjJ8nXVua7ITb-KqjXBp00HVDmqsyd7-11sX3tIAKGUueVgRCYONCp6AO6ebnxAyT9BVAWi11wHGUAw8HZfcp2B-G0PF2DxJKb8WeX66rS7UgrTHZA2zpF5N7LRJGt_hZ_iRrGpDE0SMrJ5Emw_4ZsAO_Fq-D78O88YQCrf6vZlJBVqox8v0bDY3MsM6tNb4oYTtxuifyCmLW1hHJuKA9tT_QMBPYSzuBGg#_'

        this.setState({
            pending: true
        })

        api.post('login', {
            code
        }).then((res) => {
            this.setState({
                pending: false
            })

            if (res.data.accessToken) {
                this.props.setToken(res.data.accessToken)
            }
        }).catch(() => {
            this.setState({
                pending: false
            })

        })
    }

    render() {


        const { pending } = this.state

        return (
            <div className={style.body}>
                <div className="content-fix">
                    <div className={style.logo}>
                        <img src={logo} alt="" className=""/>
                    </div>
                    <div className={style.footer}>
                        <CircleBtn  onClick={this.onLogin} disabled={pending}>
                            GO
                        </CircleBtn>
                        <p className={style.text}>
                            Sign up with Facebook to access your Instagram
                        </p>
                    </div>

                </div>

            </div>
        )
    }
}

export default Auth