import React, { Fragment, Component } from 'react';

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import api from "../../../util/api";

import 'swiper/css/swiper.css';
import Recommendation from "./index";

import eye from "../../../assets/img/recommendation/ic_round-remove-red-eye.svg";
import brain from "../../../assets/img/recommendation/mdi_brain.svg";
import spoonFork from "../../../assets/img/recommendation/si-glyph_spoon-fork.svg";
import sound from "../../../assets/img/recommendation/foundation_sound.svg";
import btnBurger from "../../../assets/img/recommendation/btn-burger.svg";
import style from "../../../components/SuccessPage/success.module.css";


class RecomendationBase extends Component{

    constructor(props) {

        super(props)

        this.state = {
            recommendationList: [],
            categoryId: null,
            categoriesVisible: false,
            categories: [
                {
                    id: null,
                    text: 'ALL'
                },
                {
                    id: 1,
                    icon: brain
                },
                {
                    id: 2,
                    icon: sound
                },
                {
                    id: 3,
                    icon: spoonFork
                },
                {
                    id: 4,
                    icon: eye
                }
            ]
        }
    }

    componentDidMount() {
        this.getRecommendation()
    }


    getRecommendation = () => {

        this.setState({
            pending: true
        })

        api.get(`recommendation`).then(({data}) => {

            this.setState({
                recommendationList: [...data, {
                    subscribeSlide: true
                }],
                pending: false
            })
        }).catch(() => {
            this.setState({
                pending: false
            })
        })
    }

    render() {

        const { recommendationList, categories } = this.state

        const settings = {
            dots: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            infinite: false,
            vertical: true,
            verticalSwiping: true,
            arrows: false,
        };
        return (

            <Fragment>
                <Slider {...settings}>
                    {recommendationList.map((item,i) => {
                        return <Recommendation {...item} key={`${item.id}-${i}`}/>
                    })}
                </Slider>
                {this.state.categoriesVisible && <div className={style.catMenu}>
                    {categories.map((cat, i) => {
                        return (
                            <div key={i} className={style.catIcon} onClick={() => {
                                this.setState({
                                    categoryId: cat.id,
                                    categoriesVisible: false
                                })
                            }}>
                                {cat.icon ? <img src={cat.icon} alt=""/> : cat.text}
                            </div>
                        )
                    })}
                </div>}

                <button className={`${style.categoryBtn}  ${this.state.categoriesVisible ? `${style.categoryBtnActive}` : ''} `}
                        onClick={() => {
                            this.setState({
                                categoriesVisible: !this.state.categoriesVisible
                            })
                        }}
                >
                    {this.state.categoryId ?
                        <Fragment>
                            <img src={this.state.categories.find(({id}) => id === this.state.categoryId).icon} alt=""/>
                        </Fragment> : <img src={btnBurger} className={style.burger} alt=""/>
                    }
                </button>
            </Fragment>
        );
    }
}

export default RecomendationBase